import { ReactComponent as BestPriceIcon } from "../Header/assest/CategoryItemImg/fire.svg";
import { ReactComponent as BestSellerIcon } from "../Header/assest/CategoryItemImg/car.svg";
import {
  HitPage,
  NewPage,
  BestPricePage,
  DeliveryPage,
  DiscountPage
} from "./CategoryItem.style";

const CategoryItem = ({ category }) => {
  switch (category) {
    case "":
      return <HitPage></HitPage>;

    case "  ":
      return <BestPricePage> </BestPricePage>;
    //
    case " ":
      return (
        <DiscountPage>         
          <BestPriceIcon /> 
        </DiscountPage>
      );
    case "":
      return <NewPage></NewPage>;
    case "":
      return (
        <DeliveryPage>
          <BestSellerIcon />  
        </DeliveryPage>
      );

    default:
      break;
  }
};

export default CategoryItem;
