import React, { useState, useEffect, useContext } from "react";
import { CategoriesLink } from "../CategoryLink/CategoryLink";

import {
  CardProduct,
  Header,
  PagePagination,
  PageCount,
} from "./CardsProduct.style";
import { CustomPagination } from "../CustomPagination/CustomPagination";
import { GetCurrentProducts } from "../CardsProduct/GetCurrentProducts";
import { DataContext } from "../Context/DataContext";

export const CardsProducts = () => {
  const [discountedProducts, setDiscountedProducts] = useState([]);
  const [bestSellingProducts, setBestSellingProducts] = useState([]);
  const [newArrivals, setNewArrivals] = useState([]);

  const { data, uniqueCategories } = useContext(DataContext);

  const [currentPage, setCurrentPage] = useState({
    discounted: 1,
    bestSelling: 1,
    newArrivals: 1,
  });
  const productsPerPage = 4;

  useEffect(() => {
    if (data) {
      const items = data;
      const discounted = items?.filter((item) =>
        item.additionalCategory.includes("  ")
      );
      setDiscountedProducts(discounted);

      const bestSelling = items?.filter((item) =>
        item.additionalCategory.includes(" ")
      );
      setBestSellingProducts(bestSelling);

      const newProducts = items?.filter((item) =>
        item.additionalCategory.includes("")
      );
      setNewArrivals(newProducts);
    }
  }, [data]);

  const paginate = (pageNumber, category) => {
    setCurrentPage((prevCurrentPage) => ({
      ...prevCurrentPage,
      [category]: pageNumber,
    }));
  };

  return (
    <>
      <PagePagination>
        <Header>  </Header>
        <PageCount>{discountedProducts.length} </PageCount>
        <CustomPagination
          productsPerPage={productsPerPage}
          totalProducts={discountedProducts.length}
          paginate={paginate}
          category="discounted"
        />
      </PagePagination>
      <CardProduct>
        {discountedProducts && discountedProducts.length > 0 ? (
          <GetCurrentProducts
            products={discountedProducts}
            category={"discounted"}
            pageNumber={currentPage.discounted}
            productsPerPage={productsPerPage}
          />
        ) : (
          <p>   </p>
        )}
      </CardProduct>
      <PagePagination>
        <Header> </Header>
        <PageCount>{bestSellingProducts.length} </PageCount>
        <CustomPagination
          productsPerPage={productsPerPage}
          totalProducts={bestSellingProducts.length}
          paginate={paginate}
          category="bestSelling"
        />
      </PagePagination>

      <CardProduct>
        {bestSellingProducts && bestSellingProducts.length > 0 ? (
          <GetCurrentProducts
            products={bestSellingProducts}
            category={"bestSelling"}
            pageNumber={currentPage.discounted}
            productsPerPage={productsPerPage}
          />
        ) : (
          <p>   </p>
        )}
      </CardProduct>
      <PagePagination>
        <Header></Header>
        <PageCount>{newArrivals.length} </PageCount>
        <CustomPagination
          productsPerPage={productsPerPage}
          totalProducts={newArrivals.length}
          paginate={paginate}
          category="newArrivals"
        />
      </PagePagination>

      <CardProduct>
        {newArrivals && newArrivals.length > 0 ? (
          <GetCurrentProducts
            products={newArrivals}
            category={"newArrivals"}
            pageNumber={currentPage.newArrivals}
            productsPerPage={productsPerPage}
          />
        ) : (
          <p>   </p>
        )}
      </CardProduct>
      <CategoriesLink uniqueCategories={uniqueCategories} />
    </>
  );
};
